.nav-item.has-notified::before,
.nav-link.has-notified::before {
    top: .45rem;
    right: .45rem;
}

.nav-item.has-notified::after,
.nav-item.has-notified::after {
    content: '';
    display: block;
    position: absolute;
    top: -.1rem;
    right: -.1rem;
    height: 1.5rem;
    width: 1.5rem;
    z-index: 10;
    border: 5px solid #fc4b6c;
    border-radius: 70px;

    animation: heartbit 1s ease-in-out;
    animation-iteration-count: infinite;
}

