// Nestable List
// ==================================================

.dd {
  position: relative;
}

.dd-list {
  position: relative;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  list-style: none;

  .dd-list {
    padding-left: 1.25rem;

    .dd-handle {
      border-left: 1px solid $list-group-border-color;
    }
  }
}

.dd-item {
  position: relative;
  display: flex;
  flex-direction: column;

  &:first-child {
    > .dd-nodrag {
      margin-top: 2rem;
    }
  }

  > button {
    position: absolute;
    left: 2rem;
    top: $list-group-item-padding-y + .375rem;
    padding: 0;
    padding-left: 20px;
    width: 20px;
    height: 20px;
    border: 0;
    line-height: 1;
    white-space: nowrap;
    vertical-align: middle;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 10px;
    overflow: hidden;

    &.dd-expand {
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg aria-hidden='true' data-prefix='fas' data-icon='caret-right' role='img' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 320 512' class='svg-inline--fa fa-caret-right fa-w-10 fa-3x'%3e%3cpath fill='currentColor' d='M0 384.662V127.338c0-17.818 21.543-26.741 34.142-14.142l128.662 128.662c7.81 7.81 7.81 20.474 0 28.284L34.142 398.804C21.543 411.404 0 402.48 0 384.662z' class=''/%3e%3c/svg%3e");
    }

    &.dd-collapse {
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg aria-hidden='true' data-prefix='fas' data-icon='caret-down' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 320 512' class='svg-inline--fa fa-caret-down fa-w-10 fa-3x'%3e%3cpath fill='currentColor' d='M31.3 192h257.3c17.8 0 26.7 21.5 14.1 34.1L174.1 354.8c-7.8 7.8-20.5 7.8-28.3 0L17.2 226.1C4.6 213.5 13.5 192 31.3 192z'/%3e%3c/svg%3e");
    }

    + .dd-handle {
      padding-left: 2.5rem;

      > .drag-indicator {
        transform: translate3d(-2rem,0,0);
        margin-right: .25rem;
      }
    }
  }
}

.dd-footer {
  display: block;
  background-color: $gray-100;
}

.dd-handle {
  margin-bottom: -1px;
  padding: $list-group-item-padding-y $list-group-item-padding-x;
  display: flex;
  align-items: center;
  background-color: $list-group-bg;
  border-top: 1px solid $list-group-border-color;
  border-bottom: 1px solid $list-group-border-color;

  @include hover-focus {
    text-decoration: none;
  }
}

.dd-expand {
  display: none;
}

.dd-collapsed .dd-list,
.dd-collapsed .dd-collapse {
  display: none;
}

.dd-collapsed .dd-expand {
  display: block;
}

.dd-placeholder {
  position: relative;
  margin: 0;
  padding: 0;
  min-height: 30px;
  background: rgba($primary, $subtle-level-hover);
  border: 1px dashed $primary;
}

.dd-empty {
  position: relative;
  margin: 0;
  padding: 0;
  min-height: 56px;
  background-color: $gray-100;
  border: 1px dashed $border-color;
}

.dd-dragel {
  position: absolute;
  pointer-events: none;
  z-index: 2000;
}
.dd-dragel > .dd-item .dd-handle {
  margin-top: 0;
}

.dd-nochildren .dd-placeholder {
  display: none;
}


// jsTree
// ==================================================

.jstree {
  // font-size: $font-size-sm;
  .fa {
    font-size: 1.1em;
  }
}
.jstree-anchor{
  color: inherit;
}

.jstree-default {
  .jstree-anchor {
    margin-top: 0;
  }
  .jstree-checkbox {
    margin-top: -2px;
  }

  .jstree-hovered,
  .jstree-wholerow-hovered {
    color: $primary;
    background: transparent;
    box-shadow: none;
    background-image: none;
  }

  .jstree-clicked,
  .jstree-wholerow-clicked {
    color: darken($primary, 5%);
    background: rgba($primary, $subtle-level-active);
    box-shadow: inset 0 0 1px $primary;
    background-image: none;
  }

  .jstree-disabled {
    color: $text-muted;
  }
}

// open state

.jstree-open > .jstree-anchor > .fa-folder:before {
  content: "\f07c";
}

// jstree context

.vakata-context,
.vakata-context ul {
  float: left;
  box-shadow: 0 6px 12px rgba($black, .175);
  border: 1px solid $border-color;
  border-radius: 4px;
  background: $white;
  // font-size: $font-size-sm;
  list-style: none;
  text-align: left;
}

// arrows

.jstree-default .jstree-node,
.jstree-default .jstree-icon {
  background-image: none;
}

.jstree-default {
  .jstree-node {
    margin-left: 21px;
  }

  .jstree-icon {
    display: inline-block;
    color: $text-muted;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
  }

  .jstree-ocl {
    padding-left: .5rem;
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
  }

  .jstree-open > .jstree-ocl:before {
    content: '\f0d7';
  }

  .jstree-closed > .jstree-ocl:before {
    content: '\f0da';
  }

  .jstree-leaf > .jstree-ocl:before {
    content: '';
  }
}

// checkbox

.jstree-default {
  .jstree-clicked .jstree-checkbox {
    border-color: $primary;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 1rem;
      height: 1rem;
      background-image: $custom-checkbox-indicator-icon-checked;
      background-repeat: no-repeat;
      background-position: 35% 35%;
      background-size: $custom-control-indicator-bg-size;
    }
  }

  .jstree-checkbox {
    position: relative;
    background: $custom-select-bg;
    border: 1px solid $input-border-color;
    box-shadow: $custom-control-indicator-box-shadow;
    border-radius: $border-radius;

    &:empty {
      margin: .25rem .25rem .25rem .125rem;
      width: 1rem;
      height: 1rem;
    }

    &.jstree-undetermined:after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 1rem;
      height: 1rem;
      background-image: $custom-checkbox-indicator-icon-indeterminate;
      background-repeat: no-repeat;
      background-position: 35% 35%;
      background-size: $custom-control-indicator-bg-size;
    }
  }

  &.jstree-checkbox-no-clicked .jstree-clicked.jstree-hovered {
    background: transparent
  }
}

// search

.jstree-default .jstree-search {
  color: $body-color;
}


