// Bootstrap Select
// ==================================================

:not(.input-group) > .bootstrap-select.custom-select:not([class*=col-]) {
  width: 100%;
}

.bootstrap-select {
  a:not([href]) {
    color: $dropdown-link-color;

    &.active,
    &:hover {
      color: $dropdown-link-hover-color;
    }
  }

  .dropdown-toggle {
    &.custom-select {
      &:after { display: none; }

      &:focus {
        outline: 0 !important;
        outline-offset: 0;
      }
    }

    &.bs-placeholder,
    &.bs-placeholder:active,
    &.bs-placeholder:focus,
    &.bs-placeholder:hover {
      color: $input-placeholder-color;
    }
  }

  > select.mobile-device:focus + .dropdown-toggle.custom-select {
    outline: 0 !important;
    outline-offset: 0;
  }

  .dropdown-menu {
    overflow: visible !important;

    li a {
      outline: 0;
    }

    .dropdown-item .text {
      font-size: inherit;
      color: inherit;
    }
  }
}


// Select2
// ==================================================

.select2-dropdown {
  z-index: $zindex-modal + 1;
}

.select2-container .select2-selection--single {
  height: $input-height;
  background: $custom-select-bg;
  @include transition(box-shadow 200ms ease-in-out, border-color 200ms ease-in-out);

  @include media-breakpoint-up(md) {
    height: $input-height-mq;
  }
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  padding-left: $input-padding-x;
  padding-right: 2rem;
  line-height: $input-height-mq;
  color: $input-color;

  @include media-breakpoint-up(md) {
    line-height: $input-height-mq;
  }
}
.select2-container--default .select2-selection--single .select2-selection__placeholder {
  color: $input-placeholder-color;
}
.select2-container--disabled .select2-selection--single .select2-selection__placeholder,
.select2-container--disabled .select2-selection--multiple .select2-selection__choice {
  color: $text-muted;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  right: 0px;
  height: $input-height-mq;
  width: 2.5rem;
  user-select: none;

  @include media-breakpoint-up(md) {
    height: calc(#{$input-height-mq} - 2px);
  }

  b {
    border-top-color: $text-muted;
  }
}
.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow {
  b {
    border-bottom-color: $text-muted;
  }
}

.select2-container .select2-selection--multiple {
  background-color: $input-bg;
  min-height: $input-height-mq;
  box-shadow: $input-box-shadow;

  @include media-breakpoint-up(md) {
    min-height: $input-height-mq;
  }
}
.select2-container--default .select2-selection--single,
.select2-container--default .select2-selection--multiple {
  border-color: $input-border-color;

  .user-avatar {
    margin-top: -.25rem;
  }
}
.select2-container--default .select2-selection--multiple .select2-selection__rendered {
  display: block;
  padding: 2px $input-padding-x;
}

.select2-container--focus .select2-selection--single,
.select2-container--default.select2-container--focus .select2-selection--multiple,
.select2-container--default.select2-container--open .select2-selection--multiple,
.select2-container--open .select2-selection--single {
  outline: 0;
  border-color: $input-focus-border-color;
  box-shadow: $input-focus-box-shadow;
}
.select2-container--default.select2-container--disabled .select2-selection--single,
.select2-container--default.select2-container--disabled .select2-selection--multiple {
  cursor: not-allowed;
  border-color: $input-border-color;
  background-color: $input-disabled-bg;
  box-shadow: none;
  .select2-search__field {
    cursor: not-allowed;
  }
}

.select2-container--open {
  .select2-dropdown--above {
    margin-top: 1px;
    box-shadow: -1px -1px 0px 0px $input-btn-focus-color, 1px 0px 0px 0px $input-btn-focus-color;
  }

  .select2-dropdown--below {
    margin-top: -1px;
    box-shadow: -1px 1px 0px 0px $input-btn-focus-color, 1px 0px 0px 0px $input-btn-focus-color;
  }
}

.select2-container--default .select2-selection--single .select2-selection__clear {
  color: $text-muted;
  z-index: 2;
  font-size: 1rem;

  .macos & {
    top: -1px;

    @include media-breakpoint-up("md") {
      top: -2px;
    }
  }

  @include hover-focus {
    color: $input-color;
  }
}

.select2-dropdown {
  background-color: $input-bg;
  border-color: $input-focus-border-color;
  box-shadow: $input-focus-box-shadow;
}
.select2-container--default .select2-search--dropdown .select2-search__field {
  padding: $input-padding-y $input-padding-x;
  color: $input-color;
  background-color: $input-bg;
  border-color: $input-border-color;
  box-shadow: $input-box-shadow;
  border-radius: $border-radius;
  box-shadow: $input-box-shadow;

  &:focus {
    outline: 0;
    border-color: $input-border-color;
  }
}
.select2-container--default .select2-results__option[aria-selected=true] {
  background-color: $gray-200;
}
.select2-container--default .select2-results__option--highlighted[aria-selected] {
  background-color: $component-active-bg;

  .text-muted {
    color: inherit !important;
  }
}
.select2-container--default .select2-results__group,
.select2-results__option {
  padding-right: $input-padding-x;
  padding-left: $input-padding-x;
}
.select2-container .select2-search--inline {
  margin-top: 2px;

  .select2-search__field {
    margin-top: 3px;
    // Placeholder
    &::placeholder {
      color: $input-placeholder-color;
    }
  }
}
.select2-container--default .select2-search--inline .select2-search__field {
  color: $input-color;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice {
  margin: 3px 12px 3px -6px;
  background-color: $gray-200;
  border-color: $gray-200;

  + .select2-search {
    margin-left: -6px;
  }
}
.select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
  margin-right: auto;
  margin-left: .25rem;
  position: relative;
  top: 4px;
  width: 1rem;
  height: 1rem;
  font-size: 1rem;
  line-height: 1rem;
  float: right;
  color: $white;
  background-color: $gray-500;
  text-align: center;
  border-radius: .5rem;

  .macos & {
    line-height: .875rem;
  }

  @include media-breakpoint-up(md) {
    top: 3px;
  }

  @include hover-focus {
    color: $white;
    background-color: $text-muted;
  }
}


