.colorpicker{
  color: $input-color;
  background-color: $input-bg;
  border: 0;
  box-shadow: $dropdown-box-shadow;

  &.colorpicker-inline {
    border: 1px solid $border-color;
    box-shadow: none;
  }
}

.colorpicker-bar {
  line-height: 1rem;
}

.colorpicker-2x {
  .colorpicker-saturation {
    width: 200px;
    height: 200px;
  }

  .colorpicker-hue,
  .colorpicker-alpha {
    width: 30px;
    height: 200px;
  }

  .colorpicker-preview,
  .colorpicker-preview div {
    height: 30px;
    font-size: 16px;
    line-height: 160%;
  }

  .colorpicker-saturation .colorpicker-guide,
  .colorpicker-saturation .colorpicker-guide i {
    height: 10px;
    width: 10px;
    border-radius: 10px;
  }
}

.colorpicker-input-addon {
  outline: 0;
}

.modal-body > .colorpicker-element {
  margin: 1rem 0;
